.bio {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    font-size: 2vh;
    line-height: 1.6;

    background-color: rgb(255, 255, 255);
}
.bio .text {
    width: 50%;
    font-size: 2vh;
    text-align: justify;
    margin: 8vh 0% 0% 0%;
}
.intro p {
    margin: 0% 0% 10vh 0%;
    text-align: justify;
}

.bio h2 {
    font-size: 2vh;
}

.bio p {
    height: auto;
    line-height: 1.6;
}
.date {
    font-weight: bold;
}
.bio ul {
    margin: 3% 0% 5% 0%;
}
.bio li {
    display: grid;
    width: 100%;
    line-height: 2;
    grid-template-columns: 30% 70%; /* Adjust the first value (100px) to control the width of the date column */
    grid-gap: 5px; /* Adjust the gap between date and event */
    align-items: start; /* Align items at the top of each grid cell */
}

/* Add any other styling you may need for the rest of the resume */

.bio img {
    margin: 5% 0% 10% 0%;
}
